@import "../inc/config";

.uk-input, .uk-select, .uk-textarea {
  width: 100%;
  background: transparent;
  border: 1px solid #9ca2a5;
  color: #fff;
  border-radius: 10px;
    font: 400 12px/16px $font;

  &:active,
  &:focus,
  &:hover {
    color:#fff;
    background: transparent;
    transition: all 0.5s;
    border-color: $color2;
  }
}

input[type="time"] {
  padding: 20px 10px;
    font: 400 15px/16px $font !important;
}

.uk-button {
  background: $color1;
  letter-spacing: 1px;
  text-transform: uppercase;
  font: 600 13px/16px $font;
  padding: 15px 0;
  width: 100%;
  border-radius: 10px;
  border: 0;
  color: #fff;

  &:focus,
  &:hover {
    transition: all 0.5s;
    background: $color2;
  }
  &.uk-button-primary {
    background: $color2;
    color:$color0;
  }
}


.bar {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;

  h1 {
    color: #9ca2a5;
    letter-spacing: 2px;
    text-transform: uppercase;
    font: 500 12px/14px $font;
  }
}



  .logo {
    color: #fff;
    letter-spacing: 1px;
    text-transform: uppercase;
    font: 600 14px/14px $font;

    span {
      margin-left: 2px;
      color: $color2;
      font-weight: 800;
    }
  }
